.sidenav-container {
  height: 100%;
}

mat-sidenav {
  width: 300px;
}

mat-sidenav-content {
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

mat-sidenav-content > button {
  margin: 10px;
  align-self: start;
}

.menu-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.mat-nav-list > div {
  border-bottom: 1px solid #e0e0e0;
}

.mat-nav-list mat-nav-list {
  padding-left: 20px;
}

.selected {
  background-color: #e0e0e0;
  /* or any highlight color */
  /* Other styles for selected item */
}

.grid-pagination-container {
  display: flex;
  flex-direction: column;
  height: 70vh;
  /* or your desired height */
}

ag-grid-angular {
  flex-grow: 1;
  /* This makes the grid take up the remaining space */
}

::ng-deep {
  .ag-body {
    margin-bottom: 0px;
  }
  .ag-icon-small-down::before {
    content: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='20' height='20' viewBox='0 0 24 24'><path d='M6 9l6 6 6-6'/></svg>") !important;
  }
}
